.modal {
  position: fixed;
  display: none;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
  background-color: @base-white;
  margin: auto;
  z-index: 20;
}

.modal-show {
  display: block;
}

.modal__wrapper {
  min-width: 320px;
}

.modal__title {
  margin: 0;
  font-size: 18px;
  line-height: 26px;

  @media (min-width: @tablet-width) {
    font-size: 30px;
  }
}

.modal__text {
  margin: 0;
  color: @brown;
  line-height: 24px;

  @media (min-width: @tablet-width) {
    br {
      display: none;
    }
  }
}

